@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    --background: 0 0% 100%;
    --foreground: 222.2 84% 4.9%;
    --card: 0 0% 100%;
    --card-foreground: 222.2 84% 4.9%;
    --popover: 0 0% 100%;
    --popover-foreground: 222.2 84% 4.9%;
    --primary: 222.2 47.4% 11.2%;
    --primary-foreground: 210 40% 98%;
    --secondary: 210 40% 96.1%;
    --secondary-foreground: 222.2 47.4% 11.2%;
    --muted: 210 40% 96.1%;
    --muted-foreground: 215.4 16.3% 46.9%;
    --accent: 210 40% 96.1%;
    --accent-foreground: 222.2 47.4% 11.2%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 210 40% 98%;
    --border: 214.3 31.8% 91.4%;
    --input: 214.3 31.8% 91.4%;
    --ring: 222.2 84% 4.9%;
    --radius: 0.5rem;
    --chart-1: 12 76% 61%;
    --chart-2: 173 58% 39%;
    --chart-3: 197 37% 24%;
    --chart-4: 43 74% 66%;
    --chart-5: 27 87% 67%;

    --burgundy-50: 350 100% 96%;
    --burgundy-100: 348 100% 92%;
    --burgundy-200: 347 96% 84%;
    --burgundy-300: 346 92% 72%;
    --burgundy-400: 345 88% 60%;
    --burgundy-500: 344 72% 48%;
    --burgundy-600: 343 72% 40%;
    --burgundy-700: 342 68% 32%;
    --burgundy-800: 341 60% 24%;
    --burgundy-900: 340 52% 16%;
  }

  .dark {
    --background: 222.2 84% 4.9%;
    --foreground: 210 40% 98%;
    --card: 222.2 84% 4.9%;
    --card-foreground: 210 40% 98%;
    --popover: 222.2 84% 4.9%;
    --popover-foreground: 210 40% 98%;
    --primary: 210 40% 98%;
    --primary-foreground: 222.2 47.4% 11.2%;
    --secondary: 217.2 32.6% 17.5%;
    --secondary-foreground: 210 40% 98%;
    --muted: 217.2 32.6% 17.5%;
    --muted-foreground: 215 20.2% 65.1%;
    --accent: 217.2 32.6% 17.5%;
    --accent-foreground: 210 40% 98%;
    --destructive: 0 62.8% 30.6%;
    --destructive-foreground: 210 40% 98%;
    --border: 217.2 32.6% 17.5%;
    --input: 217.2 32.6% 17.5%;
    --ring: 212.7 26.8% 83.9%;
    --chart-1: 220 70% 50%;
    --chart-2: 160 60% 45%;
    --chart-3: 30 80% 55%;
    --chart-4: 280 65% 60%;
    --chart-5: 340 75% 55%;
  }
}

@layer base {
  * {
    @apply border-border;
  }
  body {
    @apply bg-background text-foreground;
  }
}

@layer utilities {
  .bg-burgundy-50 {
    background-color: hsl(var(--burgundy-50));
  }
  .bg-burgundy-100 {
    background-color: hsl(var(--burgundy-100));
  }
  .bg-burgundy-200 {
    background-color: hsl(var(--burgundy-200));
  }
  .bg-burgundy-500 {
    background-color: hsl(var(--burgundy-500));
  }
  .bg-burgundy-600 {
    background-color: hsl(var(--burgundy-600));
  }
  .bg-burgundy-700 {
    background-color: hsl(var(--burgundy-700));
  }
  .text-burgundy-50 {
    color: hsl(var(--burgundy-50));
  }
  .text-burgundy-100 {
    color: hsl(var(--burgundy-100));
  }
  .text-burgundy-200 {
    color: hsl(var(--burgundy-200));
  }
  .text-burgundy-600 {
    color: hsl(var(--burgundy-600));
  }
  .text-burgundy-700 {
    color: hsl(var(--burgundy-700));
  }
  .text-burgundy-900 {
    color: hsl(var(--burgundy-900));
  }
  .border-burgundy-200 {
    border-color: hsl(var(--burgundy-200));
  }
  .hover\:bg-burgundy-50:hover {
    background-color: hsl(var(--burgundy-50));
  }
  .hover\:bg-burgundy-700:hover {
    background-color: hsl(var(--burgundy-700));
  }
  .hover\:text-burgundy-700:hover {
    color: hsl(var(--burgundy-700));
  }
}
